/**************************************
 * Pill Selector
 **************************************/
.pill-selector-hidden-input {
  position: absolute;
  visibility: hidden; }

.pill-selector-wrapper {
  margin: 0 0 20px;
  position: relative; }
  .pill-selector-wrapper .pill-selector-input-wrapper {
    -webkit-font-smoothing: antialiased;
    -webkit-transition: border-color linear .15s, box-shadow linear .15s;
    align-content: flex-start;
    align-items: flex-start;
    background-color: #fff;
    border: 1px solid #cccccc;
    border-radius: 2px;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.08);
    cursor: text;
    display: flex;
    flex-flow: row wrap;
    margin-top: 5px;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 5px;
    transition: border-color linear .15s, box-shadow linear .15s;
    width: 262px; }
    .pill-selector-wrapper .pill-selector-input-wrapper:hover {
      border: 1px solid #004aa2; }
    .pill-selector-wrapper .pill-selector-input-wrapper.is-disabled {
      background-color: #fcfcfc;
      box-shadow: none;
      color: #b5b5b5; }
      .pill-selector-wrapper .pill-selector-input-wrapper.is-disabled:hover {
        border-color: #bdc3c7; }
    .pill-selector-wrapper .pill-selector-input-wrapper.is-focused {
      border-color: #0061d5;
      box-shadow: none;
      outline: 0; }
    .pill-selector-wrapper .pill-selector-input-wrapper.show-error {
      border-color: #ed3757; }
    .pill-selector-wrapper .pill-selector-input-wrapper:not(.show-error) .icon-alert {
      display: none; }
    .pill-selector-wrapper .pill-selector-input-wrapper .icon-alert {
      bottom: 10px;
      height: 16px;
      position: absolute;
      right: 10px;
      width: 16px; }
      .pill-selector-wrapper .pill-selector-input-wrapper .icon-alert path {
        fill: #ed3757; }
    .pill-selector-wrapper .pill-selector-input-wrapper .tooltip {
      margin-top: 0 !important;
      right: 10px !important;
      top: calc(100% - 5px) !important; }
    .pill-selector-wrapper .pill-selector-input-wrapper .pills-list {
      margin: 0; }
      .pill-selector-wrapper .pill-selector-input-wrapper .pills-list li {
        display: inline-block;
        float: left; }
    .pill-selector-wrapper .pill-selector-input-wrapper .pill-selector-input,
    .pill-selector-wrapper .pill-selector-input-wrapper input[type=text].pill-selector-input {
      background-color: transparent;
      border: 0;
      box-shadow: none;
      box-sizing: content-box !important;
      color: #4f4f4f;
      flex: 1;
      height: 16px;
      margin: 0;
      min-width: 0;
      overflow: hidden;
      padding: 5px 3px 3px; }
      .pill-selector-wrapper .pill-selector-input-wrapper .pill-selector-input:focus, .pill-selector-wrapper .pill-selector-input-wrapper .pill-selector-input:hover,
      .pill-selector-wrapper .pill-selector-input-wrapper input[type=text].pill-selector-input:focus,
      .pill-selector-wrapper .pill-selector-input-wrapper input[type=text].pill-selector-input:hover {
        outline: none; }

.pill-selector-input-wrapper.show-error {
  border-color: #ed3757; }
